import React, { useState, useEffect } from "react";
import axios from "axios";
import { useHistory, Link } from "react-router-dom";
import { useDispatch } from "react-redux";

import { setCurrentEvent } from "../store/actions/paramsActions";
import changeTitle from "../utils/change-title";
import DataTable from "../components/Tables/DataTable";
import Loader from "../components/UI/Loader";
import TableActions from "../components/Buttons/TableActions";
import isEmpty from "../utils/is-empty";
import AlertSuccess from "../components/Alert/AlertSuccess";

const Events = () => {
  const [eventsState, setEventsState] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const history = useHistory();
  const dispatch = useDispatch();
  const [responseMessage, setResponseMessage] = useState("");

  const eventDeleteHandler = (e, event) => {
    e.preventDefault();

    // console.log(eventState);
    // setIsBeingProcessed(true);
    // setResponseMessage(false);
    // axios
    //   .post("/updateevent", { ...event, SessionHideYN: 1 })
    //   .then((res) => {
    //     setResponseMessage(false);
    //     if (!isEmpty(JSON.parse(res.data).EventID)) {
    //       setResponseMessage("Event deleted successfully");
    //     }
    //   })
    //   .catch((err) => {
    //     console.log(err);
    //   })
    //   .finally(() => {
    //     // setIsBeingProcessed(false);
    //   });
  };

  useEffect(() => {
    changeTitle(
      "Events List",
      { title: "Create New Event", path: "/create-event" },
      history
    );
    axios.get("geteventslist").then((res) => {
      let convertedRes = JSON.parse(res.data);
      setEventsState(convertedRes);
      setIsLoading(false);
    });
  }, [history]);

  return (
    <div>
      {responseMessage && <AlertSuccess>{responseMessage}</AlertSuccess>}
      {isLoading ? (
        <Loader />
      ) : (
        <DataTable serialNo={false} titles={["Name", "Location", "Action"]}>
          {eventsState.map((event, index) => {
            const query = `EventID=${event.EventID}&EventName=${event.EventName}&EventDescription=${event.EventDescription}&EventLocation=${event.EventLocation}&EventAddress=${event.EventAddress}&EventMap=${event.EventMap}`;
            console.log(event);
            return (
              <tr key={Math.random()}>
                <td>{event.EventName}</td>
                <td>{event.EventLocation}</td>
                <td>
                  <TableActions>
                    <button
                      onClick={() => {
                        dispatch(setCurrentEvent(event.EventName));
                        history.push(`/edit-event?${query}`);
                      }}
                      // onClick={() => {
                      //   dispatch(setCurrentEvent(event.EventName));
                      //   history.push(`/edit-event?${query}`);
                      // }}
                      className="dropdown-item"
                    >
                      Edit
                    </button>
                    <button
                      onClick={() => {
                        dispatch(setCurrentEvent(event.EventName));
                        history.push(`/price-list/${event.EventID}`);
                      }}
                      className="dropdown-item"
                    >
                      Price List
                    </button>
                    <button
                      onClick={() => {
                        dispatch(setCurrentEvent(event.EventName));
                        history.push(`/coupons/${event.EventID}`);
                      }}
                      className="dropdown-item"
                    >
                      Coupons
                    </button>
                    <button
                      onClick={() => {
                        dispatch(setCurrentEvent(event.EventName));
                        history.push(`/sessions/${event.EventID}`);
                      }}
                      className="dropdown-item"
                    >
                      Sessions
                    </button>
                    <button
                      onClick={(e) => eventDeleteHandler(e, event)}
                      className="dropdown-item"
                    >
                      Delete
                    </button>
                  </TableActions>
                </td>
              </tr>
            );
          })}
        </DataTable>
      )}
    </div>
  );
};

export default Events;
